import { refreshStoredCanvasList } from '../events';
import { canvasState } from '../store';
import { canvasReviver } from './canvas-reviver';
export const loadFromLocalStorage = (uuid) => JSON.parse(localStorage.getItem(uuid), canvasReviver);
export const saveToLocalStorage = (uuid, canvas) => localStorage.setItem(uuid, JSON.stringify(canvas));
export const load = (uuid) => {
    const data = loadFromLocalStorage(uuid);
    canvasState.loadCanvas(data);
};
export const renameSavedCanvas = (uuid, newName) => {
    const data = loadFromLocalStorage(uuid);
    data.title = newName;
    data.lastUpdated = new Date();
    saveToLocalStorage(uuid, data);
    dispatchEvent(refreshStoredCanvasList);
};
export const deleteCanvas = (uuid) => {
    localStorage.removeItem(uuid);
    dispatchEvent(refreshStoredCanvasList);
};
